.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 40px;
    font-size: 20px;
    z-index: 1;
}

.scrolling {
    background-color: rgba(236, 236, 246, 0.85);
    transition: all 0.5s ease;
}

.navbar-logo{
    display: flex;
    justify-content: space-between;
    padding: 0 50px;
    width: 100%;
}
.navbar-logo img {
    height: 80px;
    line-height: 80px;
    color: transparent;
    background-color: transparent;
    cursor: pointer;
}


.button-es{
    border: none;
    background: url('../../media/ar.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-color: transparent;
    padding: 10px 15px;
    cursor: pointer;
    height: 40px;
}

.button-en{
    border: none;
    background: url('../../media/en.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-color: transparent;
    padding: 10px 15px;
    cursor: pointer;
    height: 40px;
    margin: 0 5px;
}

.button-de{
    border: none;
    background: url('../../media/de.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-color: transparent;
    padding: 10px 15px;
    cursor: pointer;
    height: 40px;
}
.lng-container {
    top: 5px;
    width: 100%;
    text-align: center;
}

.button-donate{
    border: none;
    background-color: chocolate;
    text-align: center;
    text-decoration: none;
    font-size: 15px;
    font-family: Antropos,sans-serif;
    color: white;
    padding: 20px;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    top: 5px;
    height: 65px;
}

@media only screen and (max-width: 960px) {

    .navbar-logo{
        padding:0 5px;
    }
}