.about-container {
    background-color: var(--color_2);
    color: var(--color-1);
    padding: 100px 0 100px 0;
}

.about-desc > h3 {
    margin-bottom: 30px;
}

.about-img img {
    border-radius: 10px;
    object-fit: cover;
    max-width: 100%;
}

@media only screen and (max-width: 960px) {
    .about-container {
        padding: 4em 2em;
    }
}